import React, { useState } from "react";
import Logo from "../images/logo-06.png";
import LogoDark from "../images/logo-dark.png";
import CircleLogo from "../images/logos-circle.png";
import classnames from "classnames";

function Header() {
  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  const closeMenu = () => {
    setMenuOpen(false);
  };

  const menuClassName = menuOpen
    ? "popup-mobile-menu menu-open"
    : "popup-mobile-menu";

  const [activeItem, setActiveItem] = useState("home");

  const handleItemClick = (itemName) => {
    setActiveItem(itemName);
  };

  return (
    <div>
      <div className="header-style-2 d-block d-xl-none">
        <div className="row align-items-center">
          <div className="col-6">
            <div className="logo">
              <a href="index.html">
                <img src={LogoDark} alt="LogoDark" />
              </a>
            </div>
          </div>
          <div className="col-6">
            <div className="header-right text-end">
              <div
                className={`hamberger-menu ${menuOpen ? "menu-open" : ""}`}
                onClick={toggleMenu}
              >
                <i className="fa-solid fa-bars hamberger-menu"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className={menuClassName}>
        <div className="inner">
          <div className="menu-top">
            <div className="menu-header">
              <a className="logo" href="index.html">
                <img src={CircleLogo} alt="Personal Portfolio" />
              </a>
              <div className="close-button">
                <button
                  className="close-menu-activation close"
                  onClick={closeMenu}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    className="feather feather-x"
                  >
                    <line x1="18" y1="6" x2="6" y2="18"></line>
                    <line x1="6" y1="6" x2="18" y2="18"></line>
                  </svg>
                </button>
              </div>
            </div>
            <p className="discription">
              Inbio is a personal portfolio template. You can customize all.
            </p>
          </div>
          <div className="content">
            <ul className="primary-menu nav nav-pills">
              <li className="nav-item">
                <a
                  className={classnames("nav-link", "smoth-animation-two", {
                    active: activeItem === "home",
                  })}
                  href="#home"
                  name="home"
                  onClick={() => handleItemClick("home")}
                >
                  Home
                </a>
              </li>
              <li className="nav-item">
                <a
                  className={classnames("nav-link", "smoth-animation-two", {
                    active: activeItem === "features",
                  })}
                  href="#features"
                  name="features"
                  onClick={() => handleItemClick("features")}
                >
                  Features
                </a>
              </li>
              <li className="nav-item">
                <a
                  className={classnames("nav-link", "smoth-animation-two", {
                    active: activeItem === "portfolio",
                  })}
                  href="#portfolio"
                  name="portfolio"
                  onClick={() => handleItemClick("portfolio")}
                >
                  Portfolio
                </a>
              </li>
              <li className="nav-item">
                <a
                  className={classnames("nav-link", "smoth-animation-two", {
                    active: activeItem === "resume",
                  })}
                  href="#resume"
                  name="resume"
                  onClick={() => handleItemClick("resume")}
                >
                  Resume
                </a>
              </li>
              <li className="nav-item">
                <a
                  className={classnames("nav-link", "smoth-animation-two", {
                    active: activeItem === "clients",
                  })}
                  href="#clients"
                  name="clients"
                  onClick={() => handleItemClick("clients")}
                >
                  Clients
                </a>
              </li>
              <li className="nav-item">
                <a
                  className={classnames("nav-link", "smoth-animation-two", {
                    active: activeItem === "pricing",
                  })}
                  href="#pricing"
                  name="pricing"
                  onClick={() => handleItemClick("pricing")}
                >
                  Pricing
                </a>
              </li>
              <li className="nav-item">
                <a
                  className={classnames("nav-link", "smoth-animation-two", {
                    active: activeItem === "blog",
                  })}
                  href="#blog"
                  name="blog"
                  onClick={() => handleItemClick("blog")}
                >
                  blog
                </a>
              </li>
              <li className="nav-item">
                <a
                  className={classnames("nav-link", "smoth-animation-two", {
                    active: activeItem === "contact",
                  })}
                  href="#contact"
                  name="contact"
                  onClick={() => handleItemClick("contact")}
                >
                  Contact
                </a>
              </li>
            </ul>

            <div className="social-share-style-1 mt--40">
              <span className="title">find with me</span>
              <ul className="social-share d-flex liststyle">
                <li className="facebook">
                  <a href="#">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-facebook"
                    >
                      <path d="M18 2h-3a5 5 0 0 0-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 0 1 1-1h3z"></path>
                    </svg>
                  </a>
                </li>
                <li className="instagram">
                  <a href="#">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-instagram"
                    >
                      <rect
                        x="2"
                        y="2"
                        width="20"
                        height="20"
                        rx="5"
                        ry="5"
                      ></rect>
                      <path d="M16 11.37A4 4 0 1 1 12.63 8 4 4 0 0 1 16 11.37z"></path>
                      <line x1="17.5" y1="6.5" x2="17.51" y2="6.5"></line>
                    </svg>
                  </a>
                </li>
                <li className="linkedin">
                  <a href="#">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-linkedin"
                    >
                      <path d="M16 8a6 6 0 0 1 6 6v7h-4v-7a2 2 0 0 0-2-2 2 2 0 0 0-2 2v7h-4v-7a6 6 0 0 1 6-6z"></path>
                      <rect x="2" y="9" width="4" height="12"></rect>
                      <circle cx="4" cy="4" r="2"></circle>
                    </svg>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div className="d-none d-xl-block header-style-2">
        <header className="section-header left-header-style">
          <div className="d-flex flex-column flex-start">
            <div className="logo-wrapper">
              <img src={Logo} alt="logo" className="w-100" />
            </div>
            <div className="navbar-wrapper">
              <ul className="navbar">
                <li className="nav-item">
                  <a
                    className={classnames("nav-link", "smoth-animation-two", {
                      active: activeItem === "home",
                    })}
                    href="#home"
                    name="home"
                    onClick={() => handleItemClick("home")}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-home"
                    >
                      <path d="M3 9l9-7 9 7v11a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2z"></path>
                      <polyline points="9 22 9 12 15 12 15 22"></polyline>
                    </svg>
                    <span>Home</span>
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    className={classnames("nav-link", "smoth-animation-two", {
                      active: activeItem === "features",
                    })}
                    href="#features"
                    name="features"
                    onClick={() => handleItemClick("features")}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-briefcase"
                    >
                      <rect
                        x="2"
                        y="7"
                        width="20"
                        height="14"
                        rx="2"
                        ry="2"
                      ></rect>
                      <path d="M16 21V5a2 2 0 0 0-2-2h-4a2 2 0 0 0-2 2v16"></path>
                    </svg>
                    <span>Features</span>
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    className={classnames("nav-link", "smoth-animation-two", {
                      active: activeItem === "portfolio",
                    })}
                    href="#portfolio"
                    name="portfolio"
                    onClick={() => handleItemClick("portfolio")}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-layers"
                    >
                      <polygon points="12 2 2 7 12 12 22 7 12 2"></polygon>
                      <polyline points="2 17 12 22 22 17"></polyline>
                      <polyline points="2 12 12 17 22 12"></polyline>
                    </svg>
                    <span>Portfolio</span>
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    className={classnames("nav-link", "smoth-animation-two", {
                      active: activeItem === "resume",
                    })}
                    href="#resume"
                    name="resume"
                    onClick={() => handleItemClick("resume")}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-users"
                    >
                      <path d="M17 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2"></path>
                      <circle cx="9" cy="7" r="4"></circle>
                      <path d="M23 21v-2a4 4 0 0 0-3-3.87"></path>
                      <path d="M16 3.13a4 4 0 0 1 0 7.75"></path>
                    </svg>
                    <span>Resume</span>
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    className={classnames("nav-link", "smoth-animation-two", {
                      active: activeItem === "clients",
                    })}
                    href="#clients"
                    name="clients"
                    onClick={() => handleItemClick("clients")}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-user"
                    >
                      <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
                      <circle cx="12" cy="7" r="4"></circle>
                    </svg>
                    <span>Clients</span>
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    className={classnames("nav-link", "smoth-animation-two", {
                      active: activeItem === "pricing",
                    })}
                    href="#pricing"
                    name="pricing"
                    onClick={() => handleItemClick("pricing")}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-shopping-cart"
                    >
                      <circle cx="9" cy="21" r="1"></circle>
                      <circle cx="20" cy="21" r="1"></circle>
                      <path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"></path>
                    </svg>
                    <span>Pricing</span>
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    className={classnames("nav-link", "smoth-animation-two", {
                      active: activeItem === "blog",
                    })}
                    href="#blog"
                    name="blog"
                    onClick={() => handleItemClick("blog")}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-image"
                    >
                      <rect
                        x="3"
                        y="3"
                        width="18"
                        height="18"
                        rx="2"
                        ry="2"
                      ></rect>
                      <circle cx="8.5" cy="8.5" r="1.5"></circle>
                      <polyline points="21 15 16 10 5 21"></polyline>
                    </svg>
                    <span>blog</span>
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    className={classnames("nav-link", "smoth-animation-two", {
                      active: activeItem === "contact",
                    })}
                    href="#contact"
                    name="contact"
                    onClick={() => handleItemClick("contact")}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-message-circle"
                    >
                      <path d="M21 11.5a8.38 8.38 0 0 1-.9 3.8 8.5 8.5 0 0 1-7.6 4.7 8.38 8.38 0 0 1-3.8-.9L3 21l1.9-5.7a8.38 8.38 0 0 1-.9-3.8 8.5 8.5 0 0 1 4.7-7.6 8.38 8.38 0 0 1 3.8-.9h.5a8.48 8.48 0 0 1 8 8v.5z"></path>
                    </svg>
                    <span>Contact</span>
                  </a>
                </li>
              </ul>
            </div>
            <div className="social-connect">
              <div className="mini-title">
                <p>FIND WITH ME</p>
              </div>
              <ul className="social-icon">
                <li className="facebook">
                  <a href="#">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-facebook"
                    >
                      <path d="M18 2h-3a5 5 0 0 0-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 0 1 1-1h3z"></path>
                    </svg>
                  </a>
                </li>
                <li className="instagram">
                  <a href="#">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-instagram"
                    >
                      <rect
                        x="2"
                        y="2"
                        width="20"
                        height="20"
                        rx="5"
                        ry="5"
                      ></rect>
                      <path d="M16 11.37A4 4 0 1 1 12.63 8 4 4 0 0 1 16 11.37z"></path>
                      <line x1="17.5" y1="6.5" x2="17.51" y2="6.5"></line>
                    </svg>
                  </a>
                </li>
                <li className="linkedin">
                  <a href="#">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-linkedin"
                    >
                      <path d="M16 8a6 6 0 0 1 6 6v7h-4v-7a2 2 0 0 0-2-2 2 2 0 0 0-2 2v7h-4v-7a6 6 0 0 1 6-6z"></path>
                      <rect x="2" y="9" width="4" height="12"></rect>
                      <circle cx="4" cy="4" r="2"></circle>
                    </svg>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </header>
      </div>
    </div>
  );
}
export default Header;
